.featured {
    position: relative;
    padding: 34px 0;
    @media(min-width: $tablet) {
        padding: 50px 0;
        margin-top: 25px;
    }
    &::after {
        content: '';
        display: block;
        position: absolute;
        height: $small-wave-height-mobile;
        width: 100%;
        top: 100%;
        left: 0;
        background: url('../img/featured-wave.svg') center center repeat-x;
        background-size: auto $small-wave-height-mobile;
        @media(min-width: $tablet) {
            height: $small-wave-height-tablet;
            background-size: auto $small-wave-height-tablet;
        }
        @media(min-width: $desktop) {
            height: $small-wave-height-desktop;
            background-size: auto $small-wave-height-desktop;
        }
        @media(min-width: 1050px) {
            background: none;
        }
    }
    &__content {
        position: relative;
        @media(min-width: $tablet) {
            display: grid;
            grid-template-columns: 50% 35% 15%;
            grid-template-rows: repeat(3, auto);
            grid-row-gap: 3.3rem;
            grid-template-areas: "name name title" "product photo photo" "priceAndOrder photo photo";
        }
        @media(min-width: $desktop) {
            grid-template-columns: 12% 28% 50%;
            grid-template-rows: repeat(3, auto);
            grid-column-gap: 5%;
            grid-template-areas: none;
        }
        @media(min-width: 1050px) {
            &::after {
                content: '';
                display: block;
                position: absolute;
                height: $small-wave-height-desktop;;
                width: 100%;
                bottom: -50px - $small-wave-height-desktop;
                left: 0;
                background: url('../img/featured-wave.svg') center center repeat-x;
                background-size: auto $small-wave-height-desktop;
            }
        }
    }
    &__product-name {
        margin-bottom: 8px;
        grid-area: name;
        z-index: 1;
        @media(min-width: $tablet) {
            margin-bottom: 0;
        }
        @media(min-width: $desktop) {
            grid-area: 1 / 2 / span 1 / span 2;
            align-self: end;
        }
    }
    &__title {
        text-transform: uppercase;
        color: $green-color;
        font-weight: bold;
        grid-area: title;
        @media(min-width: $tablet) {
            text-align: center;
        }
        @media(min-width: $desktop) {
            grid-area: 1 / 1;
            align-self: end;
        }
    }
    &__product {
        margin-top: 30px;
        grid-area: product;
        @media(min-width: $tablet) {
            margin: 0 13% 0 0;
        }
        @media(min-width: $desktop) {
            grid-area: 2 / 2;
        }
    }
    &__description {
        margin-bottom: 34px;
        @media(min-width: $tablet) {
            margin-bottom: 3rem;
        }
    }
    &__characteristic {
        display: flex;
        border-bottom: 1px solid #ececec;
        height: 2.5em;
        align-items: center;
    }
    &__parameter {
        flex-basis: 40%;
    }
    &__data {
        flex-basis: 60%;
    }
    &__photo {
        grid-area: photo;
        margin: 28px 0;
        @media(min-width: $tablet) {
            margin: 0;
        }
        @media(min-width: $desktop) {
            grid-area: 1 / 3 / span 3 / span 1;
        } 
    }
    &__img {
        width: 100%;
        height: auto;
        grid-area: photo;
    }
    &__price-and-order {
        grid-area: priceAndOrder;
        @media(min-width: $tablet) {
            margin: 0;
            align-self: end;
            margin-right: 13%;
        }
        @media(min-width: $desktop) {
            grid-area: 3 / 2;
        }
    }
    &__price {
        text-align: center;
        font-size: 1em;
        font-weight: bold;
        margin-bottom: 1.75em;
        @media(min-width: $tablet) {
            font-size: 1.2em;
        }
    }
}